body.example {
    margin-bottom: 50px;
}

#example-text-area {
    margin-bottom: 15px;
}

#example-demo-area {
    display: flex;
    flex-direction: column;
    padding-bottom: 10px;
}

#example-js, #example-html {
    background-color: #f7f7f7;
    padding-top: 25px;
    padding-bottom: 20px;
}

#example-html {
    width: 100%;
    min-height: 300px;
}

#example-button-area {
    margin-top: 20px;
}

#example-nightmode-button-area {
    display: flex;
    flex: 1;
    flex-direction: row-reverse;
    margin-bottom: 20px;
}

.example-code-container {
    border: none !important;
    margin: 0;
}

.material-icons {
    font-size: 30px !important;
}

.example-logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: -10px;
    margin-left: 10px;
}

.example-logo i {
    margin-top: 10px;
}

.demo-container {
    padding: 25px 5px 25px 5px;
}

.demo {
    background-color: rgba(0, 0, 0, 0.1);
    padding: 1px 15px 10px 15px;
    height: 180px;
}

h5 {
    font-size: 20px;
}
