.sintium-drawn-feature-popup {
  display: flex;
  flex-direction: column;
}

.sintium-drawn-feature-popup > .drawn-feature-delete {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  font-weight: bold;
}

.sintium-drawn-feature-popup > .drawn-feature-delete > span {
  cursor: pointer;
}

.sintium-drawn-feature-popup > .drawn-feature-colors {
  display: flex;
  flex-direction: row;
  padding-top: 10px;
  padding-bottom: 10px;
}

.sintium-drawn-feature-popup > .drawn-feature-colors-header {
  font-weight: bold;
}

.sintium-drawn-feature-popup > .drawn-feature-colors > .drawn-feature-color {
  width: 25px;
  height: 25px;
  margin-right: 5px;
  margin-bottom: 5px;
  cursor: pointer;
}