// Sidebar layer switcher
.sintium-map-drawer-container .collapsible {
  width: 100%;
  border: none;
  margin: 0;
  box-shadow: none;
}

.sintium-map-drawer-container > .collapsible {
  border-right: 1px solid red;
}


.sintium-map-drawer-container .collapsible .entry {
  height: 3rem;
  line-height: 3rem;
  display: flex;
  align-items: stretch;
  border-bottom: 1px solid #ddd;
  color: #fff;
  background-color: #999;
}

.sintium-map-drawer-container .collapsible .layer-type {
  background-color: #000;
}

.sintium-map-drawer-container .collapsible .layer-group {
  background-color: #444;
}

.sintium-map-drawer-container .collapsible .layer {
  position: relative;
  color: #000;
  background-color: #fff;
  overflow: hidden !important;
}

.sintium-map-drawer-container .collapsible .layer-option {
  color: #fff;
  background-color: #333;
}

.header-prefix {
  min-width: 25px;
  margin: 0 0 0 4px;
}

.header-prefix i {
  width: unset;
  margin-right: unset;
  user-select: none;
}

.header-prefix .material-icons {
  width: auto;
  line-height: 3rem;
}

.header-suffix .material-icons {
  cursor: pointer;
}

.header-suffix {
  min-width: 20px;
  margin: 0 10px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-suffix [type="checkbox"]+span:not(.lever),
.header-suffix [type="radio"]+span:not(.lever)
{
  padding-left: 20px;
  display: flex;
  height: 20px;
}

.sintium-map-drawer-container .header-title {
  height: 100%;
  display: flex;
  flex-grow: 10;
}

.sintium-map-drawer-container .collapsible-header {
  line-height: unset;
  padding: 0;
  border: none;
  background-color: unset;
}

.sintium-map-drawer-container .collapsible-body {
  padding: 0;
  border: none;
}

.lock-container-div {
  position: absolute;
  left: 0;
  height: 45px;
  width: 100%;
  z-index: 1000000;
}

.lock-div {
  position: absolute;
  top: 8px;
  right: -8px;
  color: #000;
  background-color: white;
}

.lock-whiteout {
  position: absolute;
  left: 0px;
  top: 0px;
  height: 30px;
  width: 30px;
  color: #000;
  background-color: white;
}