*, *::before, *::after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

g.axis.y.invisible {
    visibility: hidden;
}

g.axis.x.invisible {
    visibility: hidden;
}


.drawer-parent {
    position: relative;
    overflow: hidden;
}

.drawer-expanded {
    width: 250px !important;
}

.drawer-container {
    background-color: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 0;
    z-index: 100;
    transition: ease-in width 225ms;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
}

.drawer-container-left {
    left: 0;
}

.sintium-drawer-container-right {
    right: 0;
}

.drawer-content-container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
}

.drawer-content {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 250px;
    overflow: hidden;
    color: white;
}

.drawer-content-left {
    right: 0;
}

.drawer-content-right {
    left: 0;
}

.drawer-arrow {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    height: 90px;
    width: 24px;
    margin-top: -45px;
    color: #fff;
    background-color: #384e58;
    cursor: pointer;
}

.drawer-arrow-left {
    margin-right: -24px;
    right: 0;
    border-radius: 0 5px 5px 0;
}

.drawer-arrow-right {
    left: 0;
    margin-left: -24px;
    border-radius: 5px 0 0 5px;
}

.select-location-switcher {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.select-location-switcher a {
    margin-left: 10px;
}
#goto {
    margin-top: 10px;
    display: flex;
    justify-content: flex-end;
}
.material-icons {
    font-size: 30px !important;
}
.logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: -10px;
    margin-left: 10px;
}
.logo i {
    margin-top: 10px;
}

.sintium-temporary-widget-storage {
    display: block;
    overflow: hidden;
}

.widget {
    display: block;
}